.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  background: #0003;
  margin-left: auto;
  margin-right: auto;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.modal {
  min-height: 450px;
  background: #fff;
  border-radius: 4px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

@media screen and (min-width: 768px) {
  .modal {
    min-width: 600px;
    min-height: 450px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    min-width: 600px;
    min-height: 450px;
  }
}

.button-close {
  width: 30px;
  height: 30px;
  border: 3px solid var(--accent-color);
  cursor: pointer;
  background: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 150px;
}

.button-close:focus, .button-close:hover {
  fill: var(--accent-color);
}

@media screen and (min-width: 768px) {
  .button-close {
    top: 8px;
    right: 8px;
  }
}

@media screen and (min-width: 1200px) {
  .button-close {
    top: 8px;
    right: 8px;
  }
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.9);
}

.icon-close {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

